import 'vant/es/time-picker/style/index';
import { ref } from 'vue';
import { calendar } from 'js-calendar-converter';

//console.log(year, month, date)

const isBlock = false;
export default {
  setup() {
    const year = new Date().getFullYear();
    const month = new Date().getMonth() + 1;
    const date = new Date().getDate();
    const currentDate = ref([year, month - 1, date]);
    console.log(currentDate.value);
    const days = 24 * 3600 * 1000;
    const showToolBar = false;
    const showTitle = false;
    const showSubtitle = false;
    let bb = "";
    let yb = "";
    let xx = '';
    const dayInterval = 5;
    const defaultTime = '2023/02/01';
    const workDate = ref([2023, 2, 1]);
    let minD = new Date(year, month - 1, 1);
    //
    let maxD = new Date(new Date(year, month, 1) - days);
    // let that=this
    //console.log(this.$refs.calendar)
    const formatter = day => {
      const year = day.date.getFullYear();
      const month = day.date.getMonth();
      const date = day.date.getDate();
      const lunar = calendar.solar2lunar(year, month, date);
      // const newDate= year+'/'+month+'/'+date
      //console.log( JSON.stringify(calendar.solar2lunar(year, month, date), null, 4));
      const distance = day.date - new Date(workDate.value.join('/'));
      //console.log(distance)
      let nl = lunar.IMonthCn + lunar.IDayCn;
      day.topInfo = nl;
      if (distance >= 0) {
        const num = Math.floor(distance / days);
        let numday = Math.floor(num % dayInterval);
        if (numday == 0) {
          day.bottomInfo = bb;
        } else if (numday == 1) {
          day.bottomInfo = yb;
        } else {
          day.bottomInfo = xx;
        }
      } else {
        const num = Math.floor(Math.abs(distance) / days);
        let numday = Math.floor(num % dayInterval);
        //console.log(numday,44444,day.date)
        if (numday == 0) {
          day.bottomInfo = bb;
        } else if (numday == 4) {
          day.bottomInfo = yb;
        } else {
          day.bottomInfo = xx;
        }
      }
      return day;
    };
    const getDate = value => {
      let time = value.selectedValues;
      let year = time[0];
      let month = time[1];
      let date = time[2];
      //let toDate=new Date(year,month,date)
      workDate.value = [year, month, date];
      currentDate.value = time;
      //this.$refs.calendar.scrollToDate(toDate);
      //console.log(value)
    };

    return {
      formatter,
      minDate: new Date(defaultTime),
      maxDate: new Date(2030, 10, 1),
      currentDate,
      getDate,
      maxD,
      minD,
      showToolBar,
      showTitle,
      isBlock,
      showSubtitle
    };
  }
};